.position-tile {
    height: 50px;
    width: 50px;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg);
    border: solid 3px rgb(9, 62, 70);    
    background-color: rgb(16, 21, 24); 
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin: 5px;
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
}
.position-tile:hover {
    border: 3px solid rgb(21, 138, 156);  
}
.position-tile.active {
    background-color: #1B2429;
    border: solid 3px #158FA4;   
}
.position-tile > .icon {
    -webkit-transform: rotate(-45deg);
        -ms-transform: rotate(-45deg);
            transform: rotate(-45deg);
    height: 37px;
    width: 37px;
}


